<template>
  <div>
    <table class="table mt-4">
      <thead>
        <tr>
        <th>圖片縮圖</th>
        <th>操作</th>
        </tr>
      </thead>
      <tbody v-if="storages.length">
        <tr
          v-for="(item, key) in storages"
          :key="key">
        <td>
          <img
          :src="item.path"
          width="100px"
          class="img-fluid">
        </td>
        <td>
          <div class="btn-group btn-group-sm">
          <button type="button" class="btn btn-outline-danger"  @click.prevent="openDeleteModal(item)">刪除</button>
          </div>
        </td>
        </tr>
      </tbody>
      </table>
      <!-- 刪除Modal -->
      <div id="delProductModal" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
              <h5 id="exampleModalLabel" class="modal-title">
                <span>刪除圖片</span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              是否刪除圖片(刪除後將無法恢復)。
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">
                取消
              </button>
              <button type="button" class="btn btn-danger" @click="deleteData(tempData.id)">
                確認刪除
              </button>
            </div>
          </div>
        </div>
      </div>
      <!-- 分頁 -->
      <Pagination :pages="pagination" @emitPages="getData"></Pagination>
  </div>
</template>

<script>
/* global $ */
import Pagination from '@/components/Pagination.vue'
export default {
  name: 'Storages',
  components: {
    Pagination
  },
  data () {
    return {
      storages: {},
      tempData: {},
      pagination: {},
      isLoading: false
    }
  },
  created () {
    this.getData()
  },
  methods: {
    getData () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/storage`
      this.$http.get(url).then((response) => {
        this.storages = response.data.data
        this.pagination = response.data.meta.pagination
        this.isLoading = false
      })
    },
    openDeleteModal (item) {
      this.tempData = Object.assign({}, item)
      $('#delProductModal').modal('show')
    },
    deleteData (id) {
      this.isLoading = true
      this.tempData.id = id
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/storage/${this.tempData.id}`
      this.$http.delete(url).then(() => {
        this.isLoading = false
        $('#delProductModal').modal('hide')
        this.getData()
      })
    }
  }
}
</script>
